// components/DealCard.tsx
"use client";

import { useState, useEffect } from "react";
import { Deal } from "../lib/api";
import Image from "next/image";

interface DealCardProps {
  deal: Deal;
  className?: string;
}

export default function DealCard({ deal, className = "" }: DealCardProps) {
  // 1. Deal Type Logic
  const isGamePass =
    deal.deal_type === "GamePass" ||
    deal.platform.includes("Xbox") ||
    deal.xboxMeta !== undefined;

  // GamePass가 아닌 경우에만 무료 여부 판단
  const isFree =
    !isGamePass &&
    (deal.deal_type === "Free" ||
      deal.sale_price === 0 ||
      deal.epicMeta?.is_free_to_keep === true);

  // 2. Image State
  const [imgSrc, setImgSrc] = useState(
    deal.image_url || "/images/default_thumbnail.png"
  );

  useEffect(() => {
    setImgSrc(deal.image_url || "/images/default_thumbnail.png");
  }, [deal.image_url]);

  const handleImageError = () => {
    if (deal.platform.toLowerCase().includes("epic")) {
      setImgSrc("/images/epic_logo.jpg");
    } else if (isGamePass || deal.platform.toLowerCase().includes("xbox")) {
      setImgSrc("/images/gamepass_logo.jpg");
    } else {
      setImgSrc("/images/default_thumbnail.png");
    }
  };

  // 3. Date Formatting (Ubisoft GMT -> KST +9h)
  const getFormattedEndDate = () => {
    if (!deal.end_date) return null;
    let endDate = new Date(deal.end_date);
    if (deal.platform.toLowerCase().includes("ubisoft")) {
      endDate = new Date(endDate.getTime() + 9 * 60 * 60 * 1000);
    }
    return endDate.toLocaleString("ko-KR", {
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      hour12: false,
    });
  };

  // --- Render Sections ---

  // A. Render for FREE GAMES (New Design)
  if (isFree) {
    return (
      <div
        className={`block group bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-all duration-300 flex flex-col h-full ${className}`}
      >
        <a
          href={deal.url}
          target="_blank"
          rel="noopener noreferrer"
          className="flex flex-col h-full"
        >
          {/* Image Area with Free Keep Badge */}
          <div className="relative w-full aspect-video bg-gray-200 overflow-hidden">
            <Image
              src={imgSrc}
              alt={deal.title}
              fill
              className="object-cover group-hover:scale-105 transition-transform duration-300"
              onError={handleImageError}
              unoptimized
            />
            {/* Free Keep Badge (Top Left) */}
            <div className="absolute top-2 left-2 bg-blue-600 text-white text-[10px] font-bold px-2 py-1 rounded shadow-sm z-10">
              Free Keep
            </div>
          </div>

          {/* Content Area */}
          <div className="p-3 flex flex-col flex-grow">
            <h3 className="text-sm font-bold text-gray-900 leading-tight line-clamp-2 mb-1 group-hover:text-blue-600 transition-colors">
              {deal.title}
            </h3>

            {/* End Date (Right Aligned) */}
            {deal.end_date && (
              <div className="flex justify-end mb-2">
                <p className="text-[11px] text-red-500 font-medium bg-red-50 px-1.5 py-0.5 rounded">
                  ~ {getFormattedEndDate()} 종료
                </p>
              </div>
            )}

            <div className="mt-auto">
              {/* Price Info (Larger Fonts) */}
              <div className="flex flex-col items-start mb-3">
                {deal.regular_price > 0 && (
                  <span className="text-sm text-gray-400 line-through">
                    ₩{deal.regular_price.toLocaleString()}
                  </span>
                )}
                <span className="text-blue-600 font-bold text-xl">무료</span>
              </div>

              {/* Button */}
              <div className="w-full text-center bg-gray-100 group-hover:bg-gray-200 text-gray-800 text-xs font-bold py-2 rounded transition-colors">
                스토어 이동
              </div>
            </div>
          </div>
        </a>
      </div>
    );
  }

  // B. Render for XBOX / SUBSCRIPTION (Reverted / Previous Design Style)
  if (isGamePass) {
    return (
      <div
        className={`block group bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-all duration-300 flex flex-col h-full ${className}`}
      >
        <a
          href={deal.url}
          target="_blank"
          rel="noopener noreferrer"
          className="flex flex-col h-full"
        >
          {/* Image Area (No Badge Overlay except Day 1) */}
          <div className="relative w-full aspect-video bg-gray-200 overflow-hidden">
            <Image
              src={imgSrc}
              alt={deal.title}
              fill
              className="object-cover group-hover:scale-105 transition-transform duration-300"
              onError={handleImageError}
              unoptimized
            />
            {/* Day 1 Badge only */}
            {deal.xboxMeta?.is_day_one && (
              <div className="absolute top-2 right-2 bg-yellow-400 text-black text-[9px] font-bold px-1.5 py-0.5 rounded shadow-sm">
                Day 1
              </div>
            )}
          </div>

          {/* Content Area (Badges inside text area like original) */}
          <div className="p-3 flex flex-col flex-grow">
            <h3 className="text-sm font-bold text-gray-900 leading-tight line-clamp-2 mb-2 group-hover:text-green-600 transition-colors">
              {deal.title}
            </h3>

            {/* Xbox Metadata / Tags */}
            <div className="mt-auto">
              <div className="flex flex-wrap gap-1 mb-2">
                <span className="px-2 py-0.5 text-[10px] font-bold text-green-800 bg-green-100 rounded-full">
                  GAME PASS
                </span>
                <span className="px-2 py-0.5 text-[10px] font-bold text-gray-600 bg-gray-100 rounded-full uppercase">
                  {deal.platform.includes("Xbox") ? "Xbox" : deal.platform}
                </span>
              </div>

              <div className="mb-2">
                <span className="text-xs font-extrabold text-green-700 uppercase block">
                  {deal.xboxMeta?.game_pass_tier.replace(/,/g, " · ") ||
                    "SUBSCRIPTION"}
                </span>
              </div>

              <div className="w-full text-center bg-green-50 group-hover:bg-green-100 text-green-800 text-xs font-bold py-1.5 rounded transition-colors">
                플레이 하기
              </div>
            </div>
          </div>
        </a>
      </div>
    );
  }

  // C. Render for STANDARD SALES (Default/Original Style)
  return (
    <div
      className={`block group bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-all duration-300 flex flex-col h-full ${className}`}
    >
      <a
        href={deal.url}
        target="_blank"
        rel="noopener noreferrer"
        className="flex flex-col h-full"
      >
        <div className="relative w-full aspect-video bg-gray-200 overflow-hidden">
          <Image
            src={imgSrc}
            alt={deal.title}
            fill
            className="object-cover group-hover:scale-105 transition-transform duration-300"
            onError={handleImageError}
            unoptimized
          />
          {/* Discount Badge */}
          {deal.discount_rate > 0 && (
            <div className="absolute bottom-2 right-2 bg-red-600 text-white text-[10px] font-bold px-1.5 py-0.5 rounded shadow-sm">
              -{deal.discount_rate}%
            </div>
          )}
        </div>

        <div className="p-3 flex flex-col flex-grow">
          <div className="flex justify-between items-start mb-1">
            <span className="px-2 py-0.5 text-[9px] font-bold rounded bg-gray-100 text-gray-600">
              {deal.platform}
            </span>
          </div>

          <h3 className="text-sm font-bold text-gray-900 leading-tight line-clamp-2 mb-1 h-10 group-hover:text-blue-600 transition-colors">
            {deal.title}
          </h3>

          <div className="mt-auto">
            <div className="flex flex-col items-start mb-2">
              {deal.regular_price > deal.sale_price && (
                <span className="text-[10px] text-gray-400 line-through">
                  ₩{deal.regular_price.toLocaleString()}
                </span>
              )}
              <span className="text-blue-600 font-bold text-sm">
                ₩{deal.sale_price.toLocaleString()}
              </span>
            </div>

            <div className="w-full text-center bg-gray-100 group-hover:bg-gray-200 text-gray-800 text-xs font-bold py-1.5 rounded transition-colors">
              스토어 이동
            </div>
          </div>
        </div>
      </a>
    </div>
  );
}
